<template>
  <div>
    <p class="title pr-5">Response</p>

    <div class="py-6" v-if="!requestInitalized">
      <div class="tile is-child box" style="background-color: #cff3ff">
        <p class="subtitle">Please make a request to retrieve results ...</p>
      </div>
    </div>
    <div class="columns" v-if="isActive">
      <div class="column">
        <p class="subtitle">Status : {{ responseStatus }}</p>
      </div>
      <div class="column">
        <p class="subtitle">Time : {{ responseTime }} <br /></p>
      </div>
      <div class="column">
        <p class="subtitle">Size : {{ responseSize }} bytes</p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
export default defineComponent({
  props: {
    requestInitalized: Array,
  },
  data() {
    return {
      isActive: this.requestInitalized[0],
      responseStatus: this.requestInitalized[1],
      responseTime: this.requestInitalized[2],
      responseSize: this.requestInitalized[3],
    };
  },
  watch: {
    requestInitalized(val, oldVal) {
      if (val !== oldVal) {
        this.isActive = this.requestInitalized[0];
        this.responseStatus = this.requestInitalized[1];
        this.responseTime = this.requestInitalized[2];
        this.responseSize = this.requestInitalized[3];
      }
    },
  },
});
</script>

