
import { defineComponent } from "vue";
export default defineComponent({
  props: {
    requestInitalized: Array,
  },
  data() {
    return {
      isActive: this.requestInitalized[0],
      responseStatus: this.requestInitalized[1],
      responseTime: this.requestInitalized[2],
      responseSize: this.requestInitalized[3],
    };
  },
  watch: {
    requestInitalized(val, oldVal) {
      if (val !== oldVal) {
        this.isActive = this.requestInitalized[0];
        this.responseStatus = this.requestInitalized[1];
        this.responseTime = this.requestInitalized[2];
        this.responseSize = this.requestInitalized[3];
      }
    },
  },
});
