<template>
  <div class="container" id="api-post">
    <div class="tile is-ancestor">
      <div class="tile is-parent">
        <article class="tile is-child box">
          <p class="title">Welcome to</p>
          <p class="subtitle">The Meme Generator!</p>
          <span
            >Click the button below to have a randomly generated meme displayed
            in the box
            <br />
            <span class="tag is-danger mt-6">May return a NSFW Response</span>
          </span>
          <div class="content ma-6">
            <button
              class="button is-fullwidth is-success"
              @click="gatherMeme()"
            >
              Generate
            </button>
            <article v-if="hasResponse" class="mt-6">
              <span class="subtitle is-size-6"
                >This program uses an API to gather random Meme Images and
                creates a Text Field over the returned image with a returned
                insult. Creating a randomly generated meme.</span
              >
            </article>
          </div>
        </article>
      </div>
      <div class="tile is-parent is-8">
        <article class="tile is-child box">
          <p class="title">Meme</p>
          <p class="subtitle">Content will display below when generated</p>
          <box-icon name="happy-heart-eyes" v-if="!hasResponse"></box-icon>
          <b v-if="hasResponse">{{ insultResponse }}</b>
          <div class="content" v-if="hasResponse">
            <img :src="memeResponse" />
          </div>
        </article>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
@import url("https://fonts.googleapis.com/css2?family=Cabin&display=swap");
body {
  height: 1200px;
  background-color: #cff3ff;
}
#api-post {
  background-color: #cff3ff;
  font-family: "Cabin";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #174e60;
}
</style>

<script lang="ts">
import { defineComponent } from "vue";
import "boxicons";
import axios from "axios";
import { insult } from "../../utils/data/insults";
import { Utils } from "../../utils/utility";

export default defineComponent({
  data() {
    return {
      insultResponse: "",
      memeResponse: "",

      hasResponse: false,
      memeQuery: [],
    };
  },
  methods: {
    gatherMeme() {
      this.hasResponse = false;
      axios
        .get("https://api.imgflip.com/get_memes")
        .then(async (response2) => {
          this.hasResponse = true;
          const x = response2.data.data.memes;
          this.memeQuery = x;
          const y = Utils.randomItem(x);
          const url = y.url;
          const reqInsult = insult();
          this.insultResponse = reqInsult;
          const newText = reqInsult.replace(/\s+/g, "%20").trim();
          const mainUrl = "https://textoverimage.moesif.com/image";
          const newUrl = "".concat(
            mainUrl,
            "?image_url=" +
              url +
              "&text=" +
              newText +
              "&overlay_color=37363b3d&text_color=f2e9e9ff&text_size=32&margin=1&y_align=bottom&x_align=center"
          );
          this.memeResponse = newUrl;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
});
</script>
