          This is a traditional game of rock paper scissors, you play against
          the AI and rack up points to win, first to 5 points is the champion.
        </p>
      </MDBModalBody>
      <MDBModalFooter>
        <MDBBtn color="info" @click="startGame()"> <b>Start Game</b> </MDBBtn>
      </MDBModalFooter>
    </MDBModal>
    <MDBContainer v-if="isGameActive" class="pt-5">
      <div>
        <div class="columns">
          <div class="column">
            <p>Player Score</p>
            <div>
              {{ playerScore }}
      
            </div>
          </div>
          <div class="column">
            <p>Computer Score</p>
            <div>
              {{ computerScore }}
      
            </div>
          </div>
        </div>

        <p v-if="showControls">What do you chose?</p>
        <MDBRow
          class="align-items-start"
          v-if="showControls"
        >
          <MDBCol>
            <button class="button is-ghost" @click="optionChoice(0)">
              <b>ROCK</b>
            </button>
            <img
              src="https://shinigamidevelopment.000webhostapp.com/img/rock.svg"
              style="width: 400px; height: 400px"
            />
          </MDBCol>
          <MDBCol>
            <button class="button is-ghost" @click="optionChoice(1)">
              <b>PAPER</b>
            </button>
            <img
              src="https://shinigamidevelopment.000webhostapp.com/img/paper.svg"
              style="width: 400px; height: 400px"
          /></MDBCol>
          <MDBCol>
            <button class="button is-ghost" @click="optionChoice(2)">
              <b>SCISSORS</b>
            </button>
            <img
              src="https://shinigamidevelopment.000webhostapp.com/img/scissors.svg"
              style="width: 400px; height: 400px"
