
import { defineComponent } from "vue";
import "boxicons";
import { useToast } from "vue-toastification";
import {
  MDBModal,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBBtn,
  MDBContainer,
  MDBRow,
  MDBCol,
} from "mdb-vue-ui-kit";


export default defineComponent({
  components: {
    MDBModal,
    MDBModalHeader,
    MDBModalTitle,
    MDBModalBody,
    MDBModalFooter,
    MDBBtn,
    MDBContainer,
    MDBRow,
    MDBCol
  },
  setup() {
    // Get toast interface
    const toast = useToast();
    return { toast };
  },
  data() {
    return {
      isActive: false,
      isGameActive: false,
      showControls: true,
      playerScore: 0,
      computerScore: 0,
      winCount: 5,
    };
  },
  methods: {
    startFunction() {
      this.isActive = true;
    },
    startGame() {
      this.isActive = !this.isActive;
      this.isGameActive = !this.isGameActive;
    },
    getRandomInt(min: number, max: number) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min) + min); //The maximum is exclusive and the minimum is inclusive
    },
    determineWin(player: number, computer: number) {
      if(this.playerScore < 5) {
        if(this.computerScore < 5) {
          console.log(true)
        switch (player) {
          case 0:
            if (computer === 0) {
              this.toast.info(`TIE ROUND!`);
          } else {
            if (computer === 1) {
              this.toast.error(`ROUND LOST!`);
              const val = this.computerScore;
              this.computerScore = val + 1;
            } else {
              if (computer === 2) {
                this.toast.success(`ROUND WIN!`);
                const val = this.playerScore;
                this.playerScore = val + 1;
              }
            }
          }
          break;
        case 1:
          if (computer === 0) {
            this.toast.success(`ROUND WIN!`);
            const val = this.playerScore;
            this.playerScore = val + 1;
          } else {
            if (computer === 1) {
              this.toast.info(`TIE ROUND!`);
            } else {
              if (computer === 2) {
                this.toast.error(`ROUND LOST!`);
          const val = this.computerScore;
              this.computerScore = val + 1;
              }
            }
          }
          break;
        case 2:
          if (computer === 0) {
            this.toast.error(`ROUND LOST!`);
          const val = this.computerScore;
              this.computerScore = val + 1;
          } else {
            if (computer === 1) {
              this.toast.success(`ROUND WIN!`);
              const val = this.playerScore;
              this.playerScore = val + 1;
            } else {
              if (computer === 2) {
                this.toast.info(`TIE ROUND!`);
              }
            }
          }
          break;
      }
      } else {
         console.log(false, 'PLAYER')
        this.showControls = false;
        }
      } else {
        console.log(false, 'COMPUTER')
         this.showControls = false;
      }
    },
    optionChoice(e: number) {
      const compChoice = this.getRandomInt(0, 2);
      switch (e) {
        case 0:
          //ROCK
          this.determineWin(0, compChoice);
          break;
        case 1:
          //PAPER
          this.determineWin(1, compChoice);
          break;
        case 2:
          //SCISSORS
          this.determineWin(2, compChoice);
          break;
      }
    },
    restartGame() {
      this.playerScore = 0
      this.computerScore = 0
      this.isActive = true
      this.isGameActive = false
      this.showControls = true
    }
  },
  mounted() {
    setInterval(() => {
      console.log(   this.isActive,
          this.isGameActive,
          this.showControls)
    }, 5000)
    setTimeout(() => {
      this.startFunction();
    }, 2000);
  },
});
