<script lang="ts">
import { defineComponent, ref } from "vue";
import "boxicons";
import axios from "axios";
import RequestColumns from "../../components/api/RequestColumns.vue";
import {
  MDBRadio,
  MDBInput,
  MDBBtn,
  MDBTabs,
  MDBTabNav,
  MDBTabItem,
  MDBTabContent,
  MDBTabPane,
  MDBTextarea,
} from "mdb-vue-ui-kit";

export default defineComponent({
  components: {
    RequestColumns,
    MDBTabs,
    MDBTabNav,
    MDBTabItem,
    MDBTabContent,
    MDBTabPane,
    MDBRadio,
    MDBInput,
    MDBBtn,
    MDBTextarea,
  },
  setup() {
    const activeTabId2 = ref("ex2-1");
    const activeTabId3 = ref("xe2-1");

    return {
      activeTabId2,
      activeTabId3,
    };
  },
  data() {
    return {
      paramsField: "",
      headerField: "",
      radioButton: "GET",
      statusResponse: 0,
      timeResponse: "",
      sizeResponse: "",
      headers: {
        "X-CoinAPI-Key": "0E0F032D-EE4A-4635-80D8-0A3E51292C64",
        "Content-Type": "application/json",
      },
      url: "",
      labelPosition: "inside",
      requestInitalized: true,
      responseHeaders: "",
      responseBody: "",
      jsonRequest: "",
      /// THIS IS FOR THE DYNAMIC INPUT FIELDS
      paramsInputs: [""],
    };
  },
  methods: {
    addNewParamField() {
      if (this.paramsInputs.length < 5) {
        this.paramsInputs.push("");
      }
    },
    async makeGetRequest() {
      let startTime = Date.now();
      const config = {
        headers: this.headers,
        params: this.paramsField,
      };
      switch (this.radioButton) {
        case "GET":
          axios
            .get(this.url, config)
            .then((response) => {
              this.resetVals();
              const resp = response;
              this.statusResponse = resp.status;
              this.responseBody = resp.data;
              this.timeResponse = this.timerFunction(startTime);
              const byteSize = (str: string) => new Blob([str]).size;
              const r = JSON.stringify(this.responseBody);
              this.sizeResponse = byteSize(r).toString();
            })
            .catch((err) => {
              this.statusResponse = err;
            });
          break;
        case "POST":
          axios
            .post(this.url, config)
            .then((response) => {
              this.resetVals();
              const resp = response;
              this.statusResponse = resp.status;
              this.responseBody = resp.data;
              this.timeResponse = this.timerFunction(startTime);
              const byteSize = (str: string) => new Blob([str]).size;
              const r = JSON.stringify(this.responseBody);
              this.sizeResponse = byteSize(r).toString();
            })
            .catch((err) => {
              this.statusResponse = err;
            });
          break;
      }
    },
    resetVals() {
      this.paramsField = "";
      this.headerField = "";
      this.radioButton = "GET";
      this.statusResponse = 0;
      this.timeResponse = "";
      this.sizeResponse = "";
      (this.headers = {
        "X-CoinAPI-Key": "0E0F032D-EE4A-4635-80D8-0A3E51292C64",
        "Content-Type": "application/json",
      }),
        (this.url = "");
      this.responseHeaders = "";
      this.responseBody = "";
      this.jsonRequest = "";
      this.paramsInputs = [""];
    },
    timerFunction(startTime: number) {
      let now = Date.now();
      let seconds = Math.floor((now - startTime) / 1000);
      let milliseconds = Math.floor((now - startTime) % 1000);
      const time = `${seconds}.${milliseconds} seconds`;
      return time;
    },
  },
});
</script>
